import React from "react";
import { allTasks } from "./task";
import { saveTasks } from "./updateTasks";

let localID =
	localStorage.getItem("lastID") === null
		? 0
		: JSON.parse(localStorage.getItem("lastID"));
export class AddTask extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			taskName: "",
			completed: false,
		};
		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(event) {
		const target = event.target;
		const value = target.type === "checkbox" ? target.checked : target.value;
		const name = target.name;

		this.setState({
			[name]: value,
		});
	}

	handleSubmit(event) {
		event.preventDefault();
		if (this.state.taskName === "") {
			return;
		}
		const obj = {
			taskName: this.state.taskName,
			completed: this.state.completed,
			ID: ++localID,
		};
		allTasks.unshift(obj);
		if (localStorage.getItem("lastID")) localStorage.removeItem("lastID");
		localStorage.setItem("lastID", JSON.stringify(localID));
		document.getElementById("addTaskID").value = "";
		document.getElementById("completedID").checked = false;
		saveTasks(true);
		this.setState({ taskName: "", completed: false });
	}

	render() {
		return (
			<form className="row addTask" onSubmit={this.handleSubmit}>
				<label>
					<input
						type="checkbox"
						name="completed"
						id="completedID"
						onChange={this.handleChange}
						defaultChecked={this.state.completed}
					/>
					<div className="checkmark"></div>
				</label>
				<input
					type="text"
					placeholder="Enter task"
					autoComplete="off"
					name="taskName"
					id="addTaskID"
					onChange={this.handleChange}
				/>
			</form>
		);
	}
}
