import "./style.css";
import React from "react";
import ReactDOM from "react-dom";
import { AddTask } from "./addTask";
import { AllTask } from "./task";
import { isAndroid } from "react-device-detect";

class DarkMode extends React.Component {
	constructor(props) {
		super(props);
		var temp = localStorage.getItem("theme")
			? localStorage.getItem("theme")
			: window.matchMedia("(prefers-color-scheme: dark)").matches
			? "dark"
			: "light";
		this.state = {
			theme: temp,
			src: temp === "dark" ? "./icon-sun.svg" : "./icon-moon.svg",
		};
		document.documentElement.setAttribute("data-theme", this.state.theme);
		this.handleChange = this.handleChange.bind(this);
	}

	handleChange(event) {
		if (this.state.theme === "light") {
			document.documentElement.setAttribute("data-theme", "dark");
			localStorage.setItem("theme", "dark");
			this.setState({
				theme: "dark",
				src: "./icon-sun.svg",
			});
		} else if (this.state.theme === "dark") {
			document.documentElement.setAttribute("data-theme", "light");
			localStorage.setItem("theme", "light");
			this.setState({
				theme: "light",

				src: "./icon-moon.svg",
			});
		}
	}

	render() {
		return (
			<label>
				<input
					type="checkbox"
					name="DarkMode"
					id="DarkModeID"
					onChange={this.handleChange}
				/>
				<img
					src={this.state.src}
					alt="DarkMode"
					id="DarkModePreview"
					className={this.state.theme + " animateDarkMode"}
				/>
			</label>
		);
	}
}

export function updateAll() {
	const r = document.getElementById("root");
	ReactDOM.render(
		<div>
			<div className="row1">
				<h1 className="mainH1">TODO</h1>
				<DarkMode />
			</div>
			<AddTask />
			<AllTask key={Math.random()} />
		</div>,
		r
	);
}

updateAll();

window.addEventListener("resize", () => {
	if (!isAndroid) updateAll();
});
