import React, { useState } from "react";
import { saveTasks } from "./updateTasks";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { updateAll } from "./index";
import cross from "./images/icon-cross.svg";

export let allTasks = [];
if (localStorage.getItem("allTasks")) {
	allTasks = JSON.parse(localStorage.getItem("allTasks"));
}

let activeFilter = 0,
	content = [];

class Task extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			taskName: this.props.taskName,
		};
		this.handleChange = this.handleChange.bind(this);
		this.removeTask = this.removeTask.bind(this);
		this.handleChangeTaskName = this.handleChangeTaskName.bind(this);
	}

	handleChange(event) {
		allTasks[content[parseInt(event.target.id)]].completed =
			event.target.checked;
		saveTasks(true);
	}

	handleChangeTaskName(event) {
		this.setState({
			taskName: event.target.value,
		});
		allTasks[content[parseInt(event.target.id.slice(2))]].taskName =
			event.target.value;
		saveTasks();
		if (event.key === "Enter") {
			event.target.blur();
		}
	}
	removeTask(event) {
		allTasks.splice(content[parseInt(event.target.id.slice(3))], 1);
		saveTasks(true);
	}
	render(props) {
		return (
			<div className="row task">
				<label>
					<input
						type="checkbox"
						name={"taskCompleted"}
						id={this.props.taskID}
						defaultChecked={this.props.completed}
						onChange={this.handleChange}
					/>
					<div className="checkmark"></div>
				</label>
				<input
					type="text"
					name={"taskText"}
					className={this.props.completed ? "completed" : null}
					id={"tN" + this.props.taskID}
					value={this.state.taskName}
					onChange={this.handleChangeTaskName}
					onKeyPress={this.handleChangeTaskName}
					autoComplete="off"
					spellCheck="false"
					disabled={this.props.completed ? true : false}
				/>
				{this.props.completed ? (
					<img
						src={cross}
						className="removeTask"
						id={"tNC" + this.props.taskID}
						onClick={this.removeTask}
						alt="Remove task"
					/>
				) : null}
			</div>
		);
	}
}

class Footer extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			itemsLeft:
				activeFilter === 0
					? allTasks.length
					: activeFilter === 1
					? allTasks.filter((d) => !d.completed).length
					: activeFilter === 2
					? allTasks.filter((d) => d.completed).length
					: null,
			activeFilterState: activeFilter,
		};
		this.handleChange = this.handleChange.bind(this);
		this.clearCompleted = this.clearCompleted.bind(this);
	}

	handleChange(event) {
		const target = event.target;
		this.setState(
			{
				activeFilterState:
					target.id === "filter0"
						? 0
						: target.id === "filter1"
						? 1
						: target.id === "filter2"
						? 2
						: null,
			},
			() => {
				activeFilter = this.state.activeFilterState;
				updateAll();
			}
		);
	}

	clearCompleted(event) {
		event.preventDefault();
		allTasks = allTasks.filter((arr) => {
			return !arr.completed;
		});
		saveTasks(true);
	}

	render() {
		if (window.innerWidth > 800)
			return (
				<React.Fragment>
					<div className="row bottomNav">
						<p className="leftItems">{this.state.itemsLeft} items left</p>
						<div className="row filters">
							<label>
								<input
									type="radio"
									name="filter"
									id="filter0"
									onChange={this.handleChange}
									defaultChecked={
										this.state.activeFilterState === 0 ? "checked" : ""
									}
								/>
								<p>All</p>
							</label>
							<label>
								<input
									type="radio"
									name="filter"
									id="filter1"
									onChange={this.handleChange}
									defaultChecked={
										this.state.activeFilterState === 1 ? "checked" : ""
									}
								/>
								<p>Active</p>
							</label>
							<label>
								<input
									type="radio"
									name="filter"
									id="filter2"
									onChange={this.handleChange}
									defaultChecked={
										this.state.activeFilterState === 2 ? "checked" : ""
									}
								/>
								<p>Completed</p>
							</label>
						</div>
						<button id="clearCompleted" onClick={this.clearCompleted}>
							Clear Completed
						</button>
					</div>
				</React.Fragment>
			);
		else
			return (
				<React.Fragment>
					<div className="row bottomNav">
						<p className="leftItems">{this.state.itemsLeft} items left</p>

						<button id="clearCompleted" onClick={this.clearCompleted}>
							Clear Completed
						</button>
					</div>
					<div className="row filters">
						<label>
							<input
								type="radio"
								name="filter"
								id="filter0"
								onChange={this.handleChange}
								defaultChecked={
									this.state.activeFilterState === 0 ? "checked" : ""
								}
							/>
							<p>All</p>
						</label>
						<label>
							<input
								type="radio"
								name="filter"
								id="filter1"
								onChange={this.handleChange}
								defaultChecked={
									this.state.activeFilterState === 1 ? "checked" : ""
								}
							/>
							<p>Active</p>
						</label>
						<label>
							<input
								type="radio"
								name="filter"
								id="filter2"
								onChange={this.handleChange}
								defaultChecked={
									this.state.activeFilterState === 2 ? "checked" : ""
								}
							/>
							<p>Completed</p>
						</label>
					</div>
				</React.Fragment>
			);
	}
}

export function AllTask(props) {
	const [tasks, updateTasks] = useState(allTasks);
	function handleDragEnd(result) {
		const items = Array.from(tasks);
		const [reorderdsItems] = items.splice(result.source.index, 1);
		items.splice(result.destination.index, 0, reorderdsItems);
		allTasks = items;
		updateTasks(items);
		saveTasks(true);
	}

	if (window.innerWidth > 800)
		return (
			<React.Fragment>
				<div className="allTasks" key={Math.random()}>
					<DragDropContext onDragEnd={handleDragEnd}>
						<Droppable droppableId="toDoTask">
							{(provided) => {
								return (
									<div {...provided.droppableProps} ref={provided.innerRef}>
										{allTasks
											.filter((con) =>
												activeFilter === 1
													? !con.completed
													: activeFilter === 2
													? con.completed
													: true
											)
											.map((item, i) => {
												content[item.ID] = i;
												return (
													<Draggable
														key={"ID" + item.ID}
														draggableId={"dragId" + item.ID}
														index={i}
													>
														{(provided) => {
															return (
																<div
																	{...provided.draggableProps}
																	{...provided.dragHandleProps}
																	ref={provided.innerRef}
																>
																	<Task
																		key={"taskKey" + item.ID}
																		taskName={item.taskName}
																		completed={item.completed}
																		taskID={item.ID}
																	/>
																</div>
															);
														}}
													</Draggable>
												);
											})}
										{provided.placeholder}
									</div>
								);
							}}
						</Droppable>
					</DragDropContext>
					<Footer />
				</div>
			</React.Fragment>
		);
	else
		return (
			<React.Fragment>
				<div className="allTasks" key={Math.random()}>
					<DragDropContext onDragEnd={handleDragEnd}>
						<Droppable droppableId="toDoTask">
							{(provided) => {
								return (
									<div {...provided.droppableProps} ref={provided.innerRef}>
										{allTasks
											.filter((con) =>
												activeFilter === 1
													? !con.completed
													: activeFilter === 2
													? con.completed
													: true
											)
											.map((item, i) => {
												content[item.ID] = i;
												return (
													<Draggable
														key={"ID" + item.ID}
														draggableId={"dragId" + item.ID}
														index={i}
													>
														{(provided) => {
															return (
																<div
																	{...provided.draggableProps}
																	{...provided.dragHandleProps}
																	ref={provided.innerRef}
																>
																	<Task
																		key={"taskKey" + item.ID}
																		taskName={item.taskName}
																		completed={item.completed}
																		taskID={item.ID}
																	/>
																</div>
															);
														}}
													</Draggable>
												);
											})}
										{provided.placeholder}
									</div>
								);
							}}
						</Droppable>
					</DragDropContext>
				</div>
				<Footer />
			</React.Fragment>
		);
}
